<template>
  <div>
    <div class="container">
      <div class="row">
        <div class="mr40 mt20">
          <router-link to="/list-user">
            <img src="../../assets/icons/back.svg" />
          </router-link>
        </div>
        <div>
          <h1 class="m0">Add User</h1>
          <p class="mt5 mb20 tc-green">
            Add new users to help you do it as soon as possible
          </p>
        </div>
      </div>
      <hr class="uline-grey" />
      <div class="mt50 row">
        <div class="w20">
          <label class="form-label">Name</label>
        </div>
        <div class="w80">
          <input
            class="form-text"
            type="text"
            placeholder="Input your full name"
          />
        </div>
      </div>
      <div class="mt30 row">
        <div class="w20">
          <label class="form-label">Username</label>
        </div>
        <div class="w80">
          <input
            class="form-text"
            type="text"
            placeholder="Input your username"
          />
        </div>
      </div>
      <div class="mt30 row">
        <div class="w20">
          <label class="form-label">Email</label>
        </div>
        <div class="w80">
          <input class="form-text" type="text" placeholder="Input your email" />
        </div>
      </div>
      <div class="mt30 row">
        <div class="w20">
          <label class="form-label">Roles</label>
        </div>
        <div class="w80">
          <select class="form-select">
            <option disabled selected>--- Select Role ---</option>
            <option value="1">Super Admin</option>
            <option value="2">Marketing</option>
            <option value="3">Sales</option>
          </select>
        </div>
      </div>
      <div class="flex mt30">
        <router-link to="/list-user">
          <button class="bt-submit-green">Add User</button>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "add-user",
};
</script>



