<template>
  <div>
    <Menu />
    <div class="container">
      <h1 class="m0">Delivery Method</h1>
      <div class="row">
        <div class="col">
          <p class="mt5 mb20 tc-green">
            Manage your Delivery Methods for the convenience of your customers
          </p>
        </div>
        <div class="col flex">
          <router-link to="/add-delivery-method">
            <button class="bt-green">+ Add</button>
          </router-link>
        </div>
      </div>
      <div class="row bg-blue mt10 p10">
        <div class="col">
          <!-- <div class="row">
            <div class="col flex">
              <span><img src="@/assets/icons/search.svg"/></span>
            </div> -->
            <div class="col flex">
              <input
                class="search-field"
                type="text"
                id="deskripsi"
                placeholder="Search by invoice number, name, amount..."
              />
            </div>
          <!-- </div> -->
        </div>
        <!-- <div class="flex">
          <div>
            <span class="px-6"><img src="@/assets/icons/filter.svg"/></span>
          </div>
          <div>
            <button class="bt-blue">
              Filter
            </button>
          </div>
        </div> -->
        <div class="col flex">
          <button class="bt-blue flex center">
            <span class="px-6"><img src="@/assets/icons/filter.svg"/></span
            >Filter
          </button>
        </div>
      </div>
      <div class="mt50">
        <table>
          <thead>
            <tr>
              <th>DELIVERY NAME</th>
              <th>DELIVERY FEE</th>
              <th>ESTIMATION DELIVERY</th>
              <th>DELIVERY TYPE IMAGE</th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="item of list_delivery_method" :key="item.id">
              <td class="center">{{ item.name }}</td>
              <td class="center">{{ item.fee }}</td>
              <td class="center">{{ item.estimation }}</td>
              <td class="center">
                <img
                  :src="require('@/assets/images/' + item.image)"
                  alt="Delivery Image"
                  width="70"
                />
              </td>
              <td class="center">
                <div class="mt5">
                  <router-link to="/edit-delivery-method">
                    <img src="@/assets/icons/edit.svg" />
                  </router-link>
                </div>
                <div class="mt5">
                  <router-link to="/detail-delivery-method">
                    <img src="@/assets/icons/detail.svg" />
                  </router-link>
                </div>
                <div class="mt5">
                  <img src="@/assets/icons/delete.svg" />
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import Menu from "@/components/menu.vue";

export default {
  name: "list-delivery-method",
  components: {
    Menu,
  },
  data() {
    return {
      list_delivery_method: [
        {
          id: "1",
          name: "REGULAR",
          fee: "Rp 9.000",
          estimation: "3-4 Hari",
          image: "regular.png",
        },
        {
          id: "2",
          name: "KARGO",
          fee: "Rp 15.000",
          estimation: "3-4 Hari",
          image: "kargo.png",
        },
        {
          id: "3",
          name: "SAMEDAY",
          fee: "Rp 9.000",
          estimation: "6-8 Jam",
          image: "sameday.png",
        },
        {
          id: "4",
          name: "INSTANT",
          fee: "Rp 20.000",
          estimation: "1-2 Jam",
          image: "instant.png",
        },
      ],
    };
  },
};
</script>



